import { Alert, Box, Grid, Theme, Typography, useMediaQuery, useTheme } from "@mui/material";
import React, { useEffect, useRef } from "react";
import { PricingCard } from "../pricing/pricingCard.tsx";
import { PriceTextTokens } from "../../textTokens/priceTextTokens.ts";
import { PageTitle } from "../pageTitle.tsx";

export const PricePage = ({scrollToPage}) => {
  const theme = useTheme();

  const pricesRef = useRef(null);
  const scrollPageInteView = () => {
    if (pricesRef !== null && pricesRef !== undefined) {
      // @ts-ignore
      pricesRef.current?.scrollIntoView({behavior: 'smooth'});
    }
  };

  const isMobileView = useMediaQuery((theme: Theme) =>
    theme.breakpoints.down("sm")
  );

 useEffect(() => {
  if (scrollToPage) {
    scrollPageInteView()
  }
 }, [scrollToPage])

  return (
    <Box
      ref={pricesRef}
      sx={{
        display: "flex",
        justifyContent: "center",
        flexDirection: "column",
        alignItems: "center",
        backgroundColor: theme.palette.background.paper,
        mt: "50px",
      }}
    >
      <PageTitle title={"raink"} linePosition={200} />
      <Box sx={{ width: "90%" }}>
        <Grid container spacing={2}>
          {PriceTextTokens.map((item) => (
            <Grid item xs={12} md={4} lg={2} >
              <PricingCard
                tableTitle={item.tableTitle}
                tableDescription={item.tableDescription || ""}
                tableData={item.tableData || []}
                cardPrice={item.cardPrice}
              />
            </Grid>
          ))}
        </Grid>
        <Alert sx={{ my: "30px" }} severity="info">
          raink forintban rtendek s az F-t tartalmazzk. Folyamatos
          megbzsnl kedvezmnyes ron dolgozunk (m^2 rakon szmlzunk)*.
          <br /> <strong>XVIII.kerleti krtya</strong> magnszemlyek esetn felhasznlhat <strong>10%
          kedvezmny</strong>re.
          <br />
          <strong>Nyugdjasoknak 10% kedvezmny</strong>t biztostunk.{" "}
          <br />
          <br />
          <Typography sx={{ fontStyle: "italic", fontSize: "14px" }}>* napi, heti, havi rendszeressggel egyedi rajnlat az albbi termkekre:</Typography>
          <Typography sx={{ fontStyle: "italic", fontSize: "14px" }}>ltalnos takarts (napi, heti rendszeressggel): br. 250ft - 450ft / m^2</Typography>
          <Typography sx={{ fontStyle: "italic", fontSize: "14px" }}>Nagytakarts (havi, kthavi rendszeressggel): br. 510ft - 640ft / m^2</Typography>
          <Typography sx={{ fontStyle: "italic", fontSize: "14px" }}>A folyamatos megbzs tartalmazza a tiszttszer-kltsget, a kiszllsi djat, illetve a gphasznlati djat. A parkolsi djat viszont <strong>nem</strong> tartalmazza!</Typography>
        </Alert>
      </Box>
    </Box>
  );
};
